.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

button {
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 1px;
  outline: 0;
  border: 1px solid white;
  cursor: pointer;
  position: relative;
  background-color: rgba(0, 0, 0, 0);
  color: black;
}

button::after {
  content: "";
  background-color: #ffe54c;
  width: 100%;
  z-index: -1;
  position: absolute;
  height: 100%;
  top: 5px;
  left: 5px;
  transition: 0.2s;
}

button:hover::after {
  top: 0px;
  left: 0px;
}

#mapp{
  left: 200px;
}

/* START HEADER */
.navigation{
  display: flex;
  position: fixed;
  width: 100%;
  z-index: 9999;
}

.tools{
  margin-left: 5%;
  width: auto;
}

.infos{
  margin-left: 15%;
  width: auto;
}

.title-nav{
  font-size: 1rem;
  font-weight: 700;
  padding: 5px 10px 5px 10px;
  color:#ffe54c;
}
/* END HEADER */


/* START INTRO_TEXT */
.box-intro_txt{
  width: 580px;
  height: 580px;
  position: fixed;
  color:white;
  padding:50px 50px;
  margin:50px 50px;
  background: linear-gradient(270deg,rgba(103, 161, 147, 0.35), rgba(218, 166, 109, 0.35), rgba(114, 109, 218, 0.35), rgba(191, 109, 218, 0.35));
    background-size: 800% 800%;
    -webkit-animation: AnimationName 5s ease infinite;
    -moz-animation: AnimationName 5s ease infinite;
    animation: AnimationName 5s ease infinite;
}
@-webkit-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@-moz-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes AnimationName { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
#title-intro_txt {
  font-size: 1rem;
  text-transform: uppercase;
}
#descr-intro_txt {
  font-size: 0.75rem;
  margin-top: 20px;
  margin-bottom: 20px;
  column-count: 2;
  column-gap: 50px;
}
/* END INTRO_TEXT */
